import React from 'react';
import {Redirect} from 'react-router-dom';
import {
  Row,
  Col,
  Button,
  Card,
  CardHeader,
  CardBody,
  Table,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader
} from 'reactstrap';
import cloneDeep from 'lodash/cloneDeep';
import Page from 'components/Page';
import { number } from 'prop-types';
import NotificationSystem from 'react-notification-system';
import {

  MdWarning,
} from 'react-icons/md';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
var timeOut;
class EnjoyPage extends React.Component {
  state = {
    // data:[
    //   {
    //     records:'',
    //     bet:'',
    //     time:'',
    //     status:0,
    //     getReult:false,
    //     modal: false,
    //     contract_money:10,
    //     guess:'',
    //     redirectToLogin:false,
    //     my_records:''
    //   },
    //   {
    //     records:'',
    //     bet:'',
    //     time:'',
    //     status:0,
    //     getReult:false,
    //     modal: false,
    //     contract_money:10,
    //     guess:'',
    //     redirectToLogin:false,
    //     my_records:''
    //   },
    //   {
    //     records:'',
    //     bet:'',
    //     time:'',
    //     status:0,
    //     getReult:false,
    //     modal: false,
    //     contract_money:10,
    //     guess:'',
    //     redirectToLogin:false,
    //     my_records:''
    //   },
    //   {
    //     records:'',
    //     bet:'',
    //     time:'',
    //     status:0,
    //     getReult:false,
    //     modal: false,
    //     contract_money:10,
    //     guess:'',
    //     redirectToLogin:false,
    //     my_records:''
    //   }
    // ],
    records:'',
    bet:'',
    time:'',
    status:0,
    getReult:false,
    modal: false,
    contract_money:10,
    guess:'',
    redirectToLogin:false,
    my_records:'',
   
    level:0
  };
  toggle = modalType => () => {
    if (!modalType) {
      return this.setState({
        modal: !this.state.modal,
      });
    }
  };
  onContractChange=(e)=>{
    this.setState({
      contract_money:e.target.value
    });
  };
  onPostState=()=>{
    fetch("/api/enjoy", {
      "method": "POST",
      "headers": {
        "content-type": "application/json",
        "Authorization":JSON.parse(localStorage.getItem('auth')).userToken
      },
      body:JSON.stringify({guess:this.state.guess,
        contract_money:this.state.contract_money,
        level:this.state.level
      })
        
    })
    .then(response =>{ 
      if(response.status<400){
        response.json().then(res=>{
          // console.log(res);
          if(res.error){
            this.notificationSystem.addNotification({
              title: <MdWarning />,
              message:res.error,
              level: 'info',
            });
          }else{
            this.setState({
              status:2
            });
          }
        });

      }else{
        this.setState({
          redirectToLogin:true
        })
      }

    });
    
  };
  componentDidMount(){
    fetch("/api/enjoy/"+this.state.level, {
      "method": "GET",
      "headers": {
        "content-type": "application/json",
        "Authorization":JSON.parse(localStorage.getItem('auth')).userToken
      }
    })
    .then(response =>{ 
      if(response.status<400){
        response.json().then(res=>{
          // console.log(res);
          if(res.time>150000){
            this.setState({
              bet:res.bet,
              records:res.records,
              my_records:res.my_records,
              time:180000-parseInt(res.time),
              status:1  
            });
            
          }else{
            this.setState({
              bet:res.bet,
              records:res.records,
              my_records:res.my_records,
              time:180000-parseInt(res.time),
              status:0
            });
          }
          timeOut=setTimeout(this.onTimeDecrease,1000);
        });

      }else{
        this.setState({
          redirectToLogin:true
        })
      }

    });
    
  }
  onTimeDecrease=()=>{
    if(this.state.time>31000){
      this.setState({
        time:this.state.time-1000
      });
      return (timeOut=setTimeout(this.onTimeDecrease,1000));
    }
    else if(this.state.time>0){
      this.setState({
        status:1,
        time:this.state.time-1000
      });
      if(!this.state.getReult){
        this.setState({
          getReult:true
        });
        fetch("/api/enjoy/"+this.state.level, {
          "method": "GET",
          "headers": {
            "content-type": "application/json",
            "Authorization":JSON.parse(localStorage.getItem('auth')).userToken
          }
        })
        .then(response =>{ 
          if(response.status<400){
            response.json().then(res=>{
              // console.log(res);
              if(res.time>150000){
                this.setState({
                  bet:res.bet,
                  records:res.records,
                  my_records:res.my_records,
                  number:res.number,
                  status:1,
                  time:this.state.time-1000
                });                              
                return (timeOut=setTimeout(this.onTimeDecrease,1000));
              }else{
                this.setState({
                  bet:res.bet,
                  records:res.records,
                  my_records:res.my_records,
                  status:0,
                  time:res.time
                });
                return (timeOut=setTimeout(this.onTimeDecrease,1000));
              }
              
            });
    
          }else{
            this.setState({
              redirectToLogin:true
            })
          }
    
        });
      }else{

        return (timeOut=setTimeout(this.onTimeDecrease,1000));
      }      
    }else{
      this.setState({
        records:'',
        bet:'',
        my_records:'',
        time:'',
        status:0,
        getReult:false,
        modal: false,
        contract_money:10,
        guess:'',
        redirectToLogin:false
      });
      fetch("/api/enjoy/"+this.state.level, {
        "method": "GET",
        "headers": {
          "content-type": "application/json",
          "Authorization":JSON.parse(localStorage.getItem('auth')).userToken
        }
      })
      .then(response =>{ 
        if(response.status<400){
          response.json().then(res=>{
            // console.log(res);
            if(res.time>150000){
              this.setState({
                bet:res.bet,
                records:res.records,
                my_records:res.my_records,
                time:180000-parseInt(res.time),
                status:1  
              });
              return (timeOut=setTimeout(this.onTimeDecrease,1000));
            }else{
              this.setState({
                bet:res.bet,
                records:res.records,
                my_records:res.my_records,
                time:180000-parseInt(res.time),
                status:0
              });
            }
            return (timeOut=setTimeout(this.onTimeDecrease,1000));
          });
  
        }else{
          this.setState({
            redirectToLogin:true
          })
        }
  
      });
      
    }
    
  }
  onButtonClick=num=>()=>{
    this.setState({
      guess:num
    });
    // console.log(num);
    return this.toggle()();
  };
  onCategoryClick=num=>()=>{
    this.setState({
      level:num
    });
    fetch("/api/enjoy/"+num, {
      "method": "GET",
      "headers": {
        "content-type": "application/json",
        "Authorization":JSON.parse(localStorage.getItem('auth')).userToken
      }
    })
    .then(response =>{ 
      if(response.status<400){
        response.json().then(res=>{
          // console.log(res);
          if(res.time>150000){
            this.setState({
              bet:res.bet,
              records:res.records,
              my_records:res.my_records,
              number:res.number,
              time:180000-parseInt(res.time),
              modal: false,
              status:1  
            });
            
          }else{
            this.setState({
              bet:res.bet,
              records:res.records,
              my_records:res.my_records,
              time:180000-parseInt(res.time),
              modal: false,
              status:0
            });
          }
          clearTimeout(timeOut);
          return (timeOut=setTimeout(this.onTimeDecrease,1000));
        });

      }else{
        this.setState({
          redirectToLogin:true
        })
      }

    });
  };
  onRecharge=()=>{
    // console.log('recharge');
  }
  render() {
    if(this.state.redirectToLogin)
      return (<Redirect to="/login" />);
    if(this.state.status==1)
      var count_down_color="red";
    else
      var count_down_color="black";
    return (
      <Page
        className="EnjoyPage"
        title={ this.state.bet && "Available Balance : "+this.state.bet[1] }
        breadcrumbs={(<Button color="link" onClick={this.onRecharge}>Recharge</Button>)}
      >
        <Row>
          <Col md="12" sm="12" xs="12">
            <Button className={this.state.level==0 && "btn-active"} color="link" onClick={this.onCategoryClick(0)}>Parity</Button>
            <Button className={this.state.level==1 && "btn-active"} color="link" onClick={this.onCategoryClick(1)}>Sapre</Button>
            <Button className={this.state.level==2 && "btn-active"} color="link" onClick={this.onCategoryClick(2)}>Bcone</Button>
            <Button className={this.state.level==3 && "btn-active"} color="link" onClick={this.onCategoryClick(3)}>Emerd</Button>
          </Col>
        </Row>
        <Row>
          <Col md="12" sm="12" xs="12">
            <Card className="mb-3">
              <CardHeader>Count Down : <span style={{'color':count_down_color}}>{parseInt(this.state.time/1000/60)+" : "+ parseInt((this.state.time/1000)%60)}</span></CardHeader>

                {
                  (this.state.status==0) ? (
                    <CardBody>
                      <Row style={{flexFlow: "row wrap","justifyContent": "space-between"}}>
                          <Button color="success" onClick={this.onButtonClick(10)}>Join Green</Button>
                          <Button color="secondary" onClick={this.onButtonClick(11)}>Join Red</Button>
                          <Button color="primary" onClick={this.onButtonClick(12)}>Join Violet</Button>
                      </Row>
                      <Row style={{flexFlow: "row wrap","justifyContent": "space-between"}}>
                          <Button onClick={this.onButtonClick(0)} color="success" style={{"border":"0px","background": "-webkit-gradient(linear,left top,right bottom,from(#fc5c7d),color-stop(50%,#fc5c7d),color-stop(51%,#6a82fb),to(#6a82fb))","background": "linear-gradient(to bottom right,#fc5c7d 0,#fc5c7d 50%,#6a82fb 51%,#6a82fb)"}}> 0 </Button>
                          <Button onClick={this.onButtonClick(1)} color="success" style={{"border":"0px"}}> 1 </Button>
                          <Button onClick={this.onButtonClick(2)} color="secondary" style={{"border":"0px"}}> 2 </Button>
                          <Button onClick={this.onButtonClick(3)} color="success" style={{"border":"0px"}}> 3 </Button>
                          <Button onClick={this.onButtonClick(4)} color="secondary" style={{"border":"0px"}}> 4 </Button>
                      </Row>
                      <Row style={{flexFlow: "row wrap","justifyContent": "space-between"}}>
                          <Button onClick={this.onButtonClick(5)} color="success" style={{"border":"0px","background": "-webkit-gradient(linear,left top,right bottom,from(#45b649),color-stop(50%,#45b649),color-stop(51%,#6a82fb),to(#6a82fb))","background": "linear-gradient(to bottom right,#45b649 0,#45b649 50%,#6a82fb 51%,#6a82fb)"}}> 5 </Button>
                          <Button onClick={this.onButtonClick(6)} color="secondary" style={{"border":"0px"}}> 6 </Button>
                          <Button onClick={this.onButtonClick(7)} color="success" style={{"border":"0px"}}> 7 </Button>
                          <Button onClick={this.onButtonClick(8)} color="secondary" style={{"border":"0px"}}> 8 </Button>
                          <Button onClick={this.onButtonClick(9)} color="success" style={{"border":"0px"}}> 9 </Button>
                      </Row>
                      <Modal
                        isOpen={this.state.modal}
                        toggle={this.toggle()}
                        className={this.props.className}>
                        <ModalHeader toggle={this.toggle()}>Contract Money</ModalHeader>
                        <ModalBody>
                          <Row>                           
                            <input type="number" value={this.state.contract_money} onChange={this.onContractChange} min="10" max="1000000" step="10" />
                          </Row>
                          
                        </ModalBody>
                        <ModalFooter>
                          <Button color="primary" onClick={this.onPostState}>
                            OK
                          </Button>{' '}
                          <Button color="secondary" onClick={this.toggle()}>
                            Cancel
                          </Button>
                        </ModalFooter>
                      </Modal>

                    </CardBody>
                  ) : (this.state.bet[4]==undefined ? (
                        <CardBody>
                          Waiting to complete...
                        </CardBody>
                      ) : (
                        <CardBody>
                          You selected : {this.state.bet[2]==10 ?  (<span className="green-circle"></span>) : (
                                  this.state.bet[2]==11 ? (<span className="red-circle"></span>) : (
                                    this.state.bet[2]==12 ? (<span className="violet-circle"></span>) : this.state.bet[2]
                                  )
                                )
                                
                                }
                          <br></br>
                          Result: {this.state.number}{' '}{(parseInt(this.state.number)%2==1) ? (
                            <span className="green-circle"></span>
                          ) : (
                            <span className="red-circle"></span>
                          )}
                          {' '}{(parseInt(this.state.number)%5==0) ? (
                            <span className="violet-circle"></span>
                          ) : ''}
                          <br />
                          You have earned : {parseInt(this.state.bet[4])-parseInt(this.state.bet[3])}
                        </CardBody>
                      )
                  )
                  
                }
              
            </Card>
          </Col>
          <Col md="6" sm="12" xs="12">
            <Card className="mb-3">
              <CardHeader>Game Record</CardHeader>
              <CardBody style={{'padding':'0 15px'}}>
                <Row style={{flexFlow: "row wrap","justifyContent": "space-between","overflow":"scroll"}}>
                  <Table {...{ 'default': true }} style={{'width':'100%'}}>
                        <thead>
                          <tr>
                            <th>Period</th>
                            <th>Price</th>
                            <th>Number</th>
                            <th>Result</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.records && this.state.records.map((ele,key)=>(
                              <tr key={key}>
                                <td>{ele.createdAt}</td>
                                <td>{ele.price}</td>
                                <td>{ele.recommend}</td>
                                <td>{parseInt(ele.recommend) % 2==1 ? (<span className="green-circle"></span>) : (<span className="red-circle"></span>)} {parseInt(ele.recommend) % 5==0 ? (<span className="violet-circle"></span>) : ""}</td>
                              </tr>
                            ))
                          }
                          </tbody>
                      </Table>
                </Row>
                

              </CardBody>
            </Card>
          </Col>
          <Col md="6" sm="12" xs="12">
            <Card className="mb-3">
              <CardHeader>My Record</CardHeader>
              <CardBody style={{'padding':'0 15px'}}>
                <Row style={{flexFlow: "row wrap","justifyContent": "space-between","overflow":"scroll"}}>
                  <Table {...{ 'default': true }}>
                        <thead>
                          <tr>
                            <th>Period</th>
                            <th>Contract money</th>
                            <th>Select</th>
                            <th>Result</th>
                            <th>Amount</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.my_records && this.state.my_records.map((ele,key)=>(
                              <tr key={key}>
                                <td>{ele.period}</td>
                                <td>{ele.contract}</td>
                                <td>{ele.select==10 ?  (<span className="green-circle"></span>) : (
                                  ele.select==11 ? (<span className="red-circle"></span>) : (
                                    ele.select==12 ? (<span className="violet-circle"></span>) : ele.select
                                  )
                                )
                                
                                }</td>
                                <td>{ele.result}{' '} {parseInt(ele.result) % 2==1 ? (<span className="green-circle"></span>) : (<span className="red-circle"></span>)} {parseInt(ele.result) % 5==0 ? (<span className="violet-circle"></span>) : ""}</td>
                              <td>{ele.amount}</td>
                              </tr>
                            ))
                          }                          
                          
                        </tbody>
                      </Table>
                </Row>
                <Row>
                  <div style={{"height":'40px'}}></div>
                </Row>

                

              </CardBody>
            </Card>
          </Col>
        </Row> 
        <NotificationSystem
          dismissible={false}
          ref={notificationSystem =>
            (this.notificationSystem = notificationSystem)
          }
          style={NOTIFICATION_SYSTEM_STYLE}
        />       
      </Page>
    );
  }
}

export default EnjoyPage;
